@use '../base/variables' as v;
@use '../base/mixins' as m;

.lineup {
    background-color: v.$rosa;
    padding: 5rem 0;

    h3, p {
        text-align: center;
        color: v.$blanco;
    }
    .dia{
        text-transform: uppercase;
        margin: 2rem 0;
    }
}
.escenarios-contenedor {
    @include m.tablet {
        @include m.grid(2, 5rem)
    }
    p {
        color: v.$negro;
    }
}

.escenario {
    padding-top: 2rem;

    .nombre-escenario {
        font-size: 3rem;

        &::after {
            content: '';
            display: block;
            margin: 2rem auto 0 auto;
            width: 20rem;
            height: 10rem;
            background-size: cover;
            background-position: center center;
            background-repeat: no-repeat;
        }
    }
}

.edm .nombre-escenario::after {
    background-image: url(../img/icono_edm.png);
}
.rock .nombre-escenario::after {
    background-image: url(../img/icono_rock.png);
}

.calendario {
    @include m.resetear-lista;
    margin: 2rem 0;
    font-weight: 700;
    span {
        font-weight: 400;
    }
    li {
        padding: 2.5rem;
    }
}

.bg-amarillo { 
    background-color: v.$amarillo;
    li:nth-child(odd){
        background-color: darken(v.$amarillo, 20%);
    }
}
.bg-verde { 
    background-color: v.$verde; 
    li:nth-child(odd){
        background-color: darken(v.$verde, 10%);
    }
}