@use '../base/variables' as v;
@use '../base/mixins' as m;

.video {
    position: relative;
    max-height: 70rem;
    min-height: 50rem;
    overflow: hidden;

    .overlay {
        position: absolute;
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        background: rgb(126,2,195);
        background: linear-gradient(270deg, rgba(126,2,195,0.5) 0%, rgba(255,0,110,0.3) 100%);
    }
    video {
        width: auto;
        @include m.desktop {
            width: 100%;
        }
        display: block;
    }
}

.contenido-video {
    z-index: 10;
    h2 {
        margin: 0;
        font-size: 7rem;
        color: v.$blanco;
    }
    p {
        display: inline-block;
        padding: 1rem 2rem;
        margin: 0;
        font-size: 2rem;
        font-weight: 700;
        color: v.$blanco;
        background-color: v.$rosa;
    }
}