@use '../base/variables' as v;
@use '../base/mixins' as m;

.header {
    background-color: v.$verde;
    .site-title {
        margin-bottom: 5rem;
        font-size: 4rem;
        font-weight: 700;
        text-align: center;
        color: v.$blanco;
        @include m.tablet {
            margin: 0;
        }
    }
    @include m.tablet {
        &.fijo {
            position: fixed;
            left: 0;
            top: 0;
            width: 100%;
            box-shadow: .1rem .15rem .3rem #333;
        }
    }
}
.contenido-header {
    display: flex;
    flex-direction: column;
    align-items: center;
    @include m.tablet {
        flex-direction: row;
        justify-content: space-between;
    }
}
.navegacion-principal {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    @include m.tablet {
        flex-direction: row;
    }
    a {
        font-size: 2.4rem;
        color: v.$blanco;

        &:hover {
            color: v.$amarillo;
        }
    }
}