// Importamos las variables del archivo _variables.scss
@use 'variables' as v; 
@use '../base/mixins' as m;

html {
    font-size: 62.5%;
    box-sizing: border-box;
    scroll-behavior: smooth;
}
*, *::before, *::after {
    padding: 0;
    margin: 0;
    box-sizing: inherit;
    text-decoration: none;
}
body {
    font-family: v.$fuente-principal;
    font-size: 1.6rem;

    &.fijar-body {
        overflow: hidden;
    }
    @include m.tablet {
        &.body-scroll {
            padding-top: 8.6rem;
        }
    }
}

// Estilos Headers y parrafos
h1, h2, h3 { margin: 0 0 5rem 0; }
h1 { font-size: 4rem; }
h2 { font-size: 3.6rem; }
h3 { font-size: 3rem; }

p {
    font-size: 2rem;
    color: v.$negro;
    line-height: 1.5;
}

// Ancho y Posicionamiento del contenedor
.contenedor {
    width: 95%;
    max-width: 120rem;
    margin: 0 auto;
    padding: 2rem;
}


img {
    display: block;
    max-width: 100%;
    width: 100%;
    height: auto;
}