@use '../base/variables' as v;
@use '../base/mixins' as m;

.boletos {
    padding: 5rem 0;
    h3 {
        margin-bottom: 2rem;
        text-align: center;
        color: v.$blanco;
    }
}

.contenedor-boletos{
    @include m.tablet {
        @include m.grid(2, 2rem)
    }
}
.pase {
    margin-bottom: 5rem;
    @include m.tablet {
        margin: 0;        
    }
    .pase-body {
        @include m.resetear-lista;
        li, :nth-child(odd) {
            padding: 1rem;
            background-color: v.$blanco;
            border-bottom: 1px solid #e1e1e1;
        }
    }
    // Elementos <p>
    p {
        font-size: 2.4rem;
        text-align: center;
        color: v.$blanco;
    }
    .nombre-pase {
        margin-bottom: 1.5rem;
    }
    p:nth-child(2), p:last-child { 
        padding: 1rem 0; 
        text-transform: uppercase;
    }
    p:nth-child(2) {
        font-size: 1.6rem;
        background-color: v.$rosa;
    }
    p:last-child{
        font-weight: 700;
        background-color: v.$morado;
    }
}